<template>
  <div class="hello">
    <div class="navDiv">时间计算</div>
    <van-cell title="开始时间" :value="startTime" @click="startFn"/>
    <van-cell title="结束时间" :value="endTime" @click="endFn"/>
    <van-cell title="相差时间" :value="seTime" />
    <van-button class="vanBtn" color="linear-gradient(to right, rgb(102, 188, 255), rgb(157, 142, 255))" @click="submitBtn">计算</van-button>
    <!-- 开始时间 -->
    <van-action-sheet v-model="startFlag">
        <van-datetime-picker
            v-model="nowDate"
            type="date"
            @cancel="cancel1"
            @confirm="confirm1"
        />
    </van-action-sheet>
    <!-- 结束时间 -->
    <van-action-sheet v-model="endFlag">
        <van-datetime-picker
            v-model="nowDate"
            type="date"
            @cancel="cancel2"
            @confirm="confirm2"
        />
    </van-action-sheet>
  </div>
</template>

<script>
export default {
    name: 'HelloWorld',
    props: {
        msg: String
    },
    data(){
        return {
            startFlag:false,
            endFlag:false,
            startTime:"",
            endTime:"",
            sForm:{
                sy:"",
                sm:"",
                sd:"",
            },
            eForm:{
                ey:"",
                em:"",
                ed:"",
            },
            nowDate: new Date(),
            seTime:""
        }
    },
    watch:{
        'startTime'(){
            this.seTime = ''
        },
        'endTime'(){
            this.seTime = ''
        }
    },
    methods: {
        submitBtn(){
            let startDay=new Date(this.sForm.sy,this.sForm.sm-1,this.sForm.sd);
            let endDay=new Date(this.eForm.ey,this.eForm.em-1,this.eForm.ed);
            this.seTime = (endDay-startDay)/86400000+"天"
        },
        startFn(){
            this.startFlag = true
        },
        endFn(){
            this.endFlag = true
        },
        timeFn(value,type){
            let date = value;
            let m = date.getMonth() + 1;
            let d = date.getDate();
            if (m >= 1 && m <= 9) {
                m = "0" + m;
            }
            if (d >= 0 && d <= 9) {
                d = "0" + d;
            }
            let timer = date.getFullYear() + "年" + m + "月" + d + "日"
            if(type=='start'){
                this.sForm.sy = date.getFullYear()
                this.sForm.sm = m
                this.sForm.sd = d
                this.startTime = timer
            }else{
                this.eForm.ey = date.getFullYear()
                this.eForm.em = m
                this.eForm.ed = d
                this.endTime = timer
            }
        },
        confirm1(value){
            this.timeFn(value,'start')
            this.startFlag = false
        },
        cancel1(){
            this.startFlag = false
        },
        confirm2(value){
            this.timeFn(value,'end')
            this.endFlag = false
        },
        cancel2(){
            this.endFlag = false
        },
    },
}   
</script>
<style scoped>
.hello{width: 100%;height: 100%;}
.navDiv{height: 60px;
    line-height: 60px;
    font-weight: 500;
    text-align: center;
    color: #323233;
    margin-bottom: 20px;
    background: white;}
.vanBtn{margin-top:20px;    width: 50%;
    margin-left: 25%;}
</style>
